import VueRouter from 'vue-router'

//引入组件
import Index from '../pages'
import Home from '../pages/home/home'
import Login from '../pages/login/login'
import Register from '../pages/register/register'
import Center from '../pages/center/center'
import Forum from '../pages/forum/list'
import ForumAdd from '../pages/forum/add'
import ForumDetail from '../pages/forum/detail'
import MyForumList from '../pages/forum/myForumList'
import Storeup from '../pages/storeup/list'
import AddrList from '../pages/shop-address/list'
import AddrAdd from '../pages/shop-address/addOrUpdate'
import Order from '../pages/shop-order/list'
import OrderConfirm from '../pages/shop-order/confirm'
import Cart from '../pages/shop-cart/list'
import News from '../pages/news/news-list'
import NewsDetail from '../pages/news/news-detail'
import yonghuList from '../pages/yonghu/list'
import yonghuDetail from '../pages/yonghu/detail'
import yonghuAdd from '../pages/yonghu/add'
import yinshifenleiList from '../pages/yinshifenlei/list'
import yinshifenleiDetail from '../pages/yinshifenlei/detail'
import yinshifenleiAdd from '../pages/yinshifenlei/add'
import shipinxinxiList from '../pages/shipinxinxi/list'
import shipinxinxiDetail from '../pages/shipinxinxi/detail'
import shipinxinxiAdd from '../pages/shipinxinxi/add'
import jiankanghuodongList from '../pages/jiankanghuodong/list'
import jiankanghuodongDetail from '../pages/jiankanghuodong/detail'
import jiankanghuodongAdd from '../pages/jiankanghuodong/add'
import fenleiList from '../pages/fenlei/list'
import fenleiDetail from '../pages/fenlei/detail'
import fenleiAdd from '../pages/fenlei/add'
import gerencepingList from '../pages/gerenceping/list'
import gerencepingDetail from '../pages/gerenceping/detail'
import gerencepingAdd from '../pages/gerenceping/add'
import dazhongcepingList from '../pages/dazhongceping/list'
import dazhongcepingDetail from '../pages/dazhongceping/detail'
import dazhongcepingAdd from '../pages/dazhongceping/add'
import pingfenxinxiList from '../pages/pingfenxinxi/list'
import pingfenxinxiDetail from '../pages/pingfenxinxi/detail'
import pingfenxinxiAdd from '../pages/pingfenxinxi/add'
import jiankangyinshiList from '../pages/jiankangyinshi/list'
import jiankangyinshiDetail from '../pages/jiankangyinshi/detail'
import jiankangyinshiAdd from '../pages/jiankangyinshi/add'
import systemintroList from '../pages/systemintro/list'
import systemintroDetail from '../pages/systemintro/detail'
import systemintroAdd from '../pages/systemintro/add'

const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err)
}

//配置路由
export default new VueRouter({
	routes:[
		{
      path: '/',
      redirect: '/index/home'
    },
		{
			path: '/index',
			component: Index,
			children:[
				{
					path: 'home',
					component: Home
				},
				{
					path: 'center',
					component: Center,
				},
				{
					path: 'forum',
					component: Forum
				},
				{
					path: 'forumAdd',
					component: ForumAdd
				},
				{
					path: 'forumDetail',
					component: ForumDetail
				},
				{
					path: 'myForumList',
					component: MyForumList
				},
				{
					path: 'storeup',
					component: Storeup
				},
                {
                    path: 'shop-address/list',
                    component: AddrList
                },
                {
                    path: 'shop-address/addOrUpdate',
                    component: AddrAdd
                },
				{
					path: 'shop-order/order',
					component: Order
				},
				{
					path: 'cart',
					component: Cart
				},
				{
					path: 'shop-order/orderConfirm',
					component: OrderConfirm
				},
				{
					path: 'news',
					component: News
				},
				{
					path: 'newsDetail',
					component: NewsDetail
				},
				{
					path: 'yonghu',
					component: yonghuList
				},
				{
					path: 'yonghuDetail',
					component: yonghuDetail
				},
				{
					path: 'yonghuAdd',
					component: yonghuAdd
				},
				{
					path: 'yinshifenlei',
					component: yinshifenleiList
				},
				{
					path: 'yinshifenleiDetail',
					component: yinshifenleiDetail
				},
				{
					path: 'yinshifenleiAdd',
					component: yinshifenleiAdd
				},
				{
					path: 'shipinxinxi',
					component: shipinxinxiList
				},
				{
					path: 'shipinxinxiDetail',
					component: shipinxinxiDetail
				},
				{
					path: 'shipinxinxiAdd',
					component: shipinxinxiAdd
				},
				{
					path: 'jiankanghuodong',
					component: jiankanghuodongList
				},
				{
					path: 'jiankanghuodongDetail',
					component: jiankanghuodongDetail
				},
				{
					path: 'jiankanghuodongAdd',
					component: jiankanghuodongAdd
				},
				{
					path: 'fenlei',
					component: fenleiList
				},
				{
					path: 'fenleiDetail',
					component: fenleiDetail
				},
				{
					path: 'fenleiAdd',
					component: fenleiAdd
				},
				{
					path: 'gerenceping',
					component: gerencepingList
				},
				{
					path: 'gerencepingDetail',
					component: gerencepingDetail
				},
				{
					path: 'gerencepingAdd',
					component: gerencepingAdd
				},
				{
					path: 'dazhongceping',
					component: dazhongcepingList
				},
				{
					path: 'dazhongcepingDetail',
					component: dazhongcepingDetail
				},
				{
					path: 'dazhongcepingAdd',
					component: dazhongcepingAdd
				},
				{
					path: 'pingfenxinxi',
					component: pingfenxinxiList
				},
				{
					path: 'pingfenxinxiDetail',
					component: pingfenxinxiDetail
				},
				{
					path: 'pingfenxinxiAdd',
					component: pingfenxinxiAdd
				},
				{
					path: 'jiankangyinshi',
					component: jiankangyinshiList
				},
				{
					path: 'jiankangyinshiDetail',
					component: jiankangyinshiDetail
				},
				{
					path: 'jiankangyinshiAdd',
					component: jiankangyinshiAdd
				},
				{
					path: 'systemintro',
					component: systemintroList
				},
				{
					path: 'systemintroDetail',
					component: systemintroDetail
				},
				{
					path: 'systemintroAdd',
					component: systemintroAdd
				},
			]
		},
		{
			path: '/login',
			component: Login
		},
		{
			path: '/register',
			component: Register
		},
	]
})
